export default {
	data() {
		return {
			tempFace: ""
		};
	},
	onLoad(params) {
		var tempFilePath = params.tempFilePath;
		this.tempFace = tempFilePath;
	},
	methods: {
		// 重新选择头像
		changePendingFace() {
			var me = this;
			uni.chooseImage({
				count: 1,
				sizeType: ["compressed"],
				sourceType: ["album"],
				success(res) {
					// 获得临时路径
					var tempFilePath = res.tempFilePaths[0];
					me.tempFace = tempFilePath;
				}
			})
		},
		upload() {
			var me = this;
			var globalUser = me.getGlobalUser("globalUser");
			
			uni.showLoading({
				mask: true,
				title: "上传中，请稍后"
			});
			
			uni.uploadFile({
				url: me.serverUrl + "/user/uploadFace?userId=" + globalUser.id,
				filePath: me.tempFace,
				name: "file",
				header: {
					"headerUserId": globalUser.id,
					"headerUserToken": globalUser.userUniqueToken
				},
				success(res) {
					// debugger;
					var resDataStr = res.data;
					// console.log(typeof(resDataStr));
					var resData = JSON.parse(resDataStr);
					// console.log(typeof(resData));
					if (resData.status == 200) {
						// 获得最新的用户数据
						var userInfo = resData.data;
						uni.setStorageSync("globalUser", userInfo);
						uni.navigateBack({
							delta: 1
						})
					} else if (resData.status == 502 || resData.status == 500) {
						uni.showToast({
							title: res.data.msg,
							image: "../../static/icos/error.png",
							duration: 2000
						})
					}
				},
				complete() {
					uni.hideLoading();
				}
			});
			
		}
	}
}
